let wrapper;

const sayHi = () => console.log("hi");

const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

async function writingAll() {
  wrapper = document.querySelector("[data-text]");
  const stringsContainer = document.querySelectorAll(".item");

  stringsContainer.forEach(
    (string = async () => {
      let str = string.textContent;
      await write(str);
      await sleep(2000);
      await erase();
      await sleep(1000);
    })
  );
}

async function write(text) {
  let index = 0;
  while (index < text.length) {
    //await sleep(100);
    index++;
    wrapper.innerHTML = text.substring(0, index);
  }
}

async function erase() {
  while (wrapper.textContent.length) {
    const timeout = 100;
    await sleep(timeout);
    wrapper.textContent = wrapper.textContent.substring(
      0,
      wrapper.textContent.length - 2
    );
  }
}

write("wowowo");
